.modal-pane {
  font-family: "Poppins", sans-serif;
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  box-sizing: border-box;
  background: rgba(0,0,0,0.2);
  padding: 5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: auto;
  color: #333;
  font-size: 3.2rem;
  font-weight: 600;

  a {
    color: inherit;
    text-decoration: none;
  }

  > div {
    background: #fff;
    flex: 0 0 auto;
    padding: 5rem;
    border-radius: 5rem;
    overflow: hidden;
    box-shadow: 0 0 2rem 1rem rgba(0,0,0,0.1) inset;
    max-width: 90vw;
    box-sizing: border-box;

    h3 {
      margin: -5rem -5rem 5rem -5rem;
      padding: 3rem 5rem;
      font-size: 3.6rem;
      text-align: center;
      background: #fff;
      border-bottom: 0.2rem solid #707070;
      box-shadow: 0 -1rem 2rem 0 rgba(0,0,0,0.1) inset;
      font-weight: bold;
    }

    .footer-buttons {
      margin: 5rem -5rem -5rem -5rem;
      font-size: 3.6rem;
      text-align: center;
      background: #fff;
      border-top: 0.2rem solid #707070;
      box-shadow: 0 -1rem 2rem 0 rgba(0,0,0,0.1) inset;
      display: flex;
      flex-direction: row;
      align-items: stretch;
      justify-content: stretch;

      .button {
        padding: 3rem 5rem;
        flex: 1 1 100%;
        font-size: 2.8rem;
        border-right: 0.2rem solid #707070;
        background: rgba(255,255,255,0.01);

        &:last-child {
          border: 0;
        }

        &:active {
          background: #fff;
          outline: 1px solid #000;
        }
      }
    }
  }
  .modal {display: block;}
}

.modal {display: none;}

#chainModal {

  .swap-icons {
    width: 100%;
    height: auto;
  }

  .chain-names {
    display: flex;
    flex-direction: row;
    font-size: 3.2rem;
    text-align: center;
    font-weight: 600;
    justify-content: space-between;

    div {
      flex: 0 0 45%;
    }
  }

  .chain-arrow {
    fill: #B9B9B9;
    stroke: #707070;
  }

  .chain-logo {
    // fill: url("#chain-from-image");
    stroke: #707070;
    stroke-width: 3px;

  }

  #chain-to-logo {
    // fill: url("#chain-to-image");
  }
}

#deeplinks {
  .wallet-options {
    display: flex;
    flex-direction: row;
    padding: 2rem;

    > * {
      align-items: center;
      display: flex;
      flex-direction: column;
      margin: 2rem;
      width: 20rem;

      &:before {
        height: 20rem;
        width: 20rem;
        content: '';
        background-size: contain;
        background-repeat: no-repeat;
        flex: 0 0 auto;
      }
    }
    .trustwallet {
      &:before {
        background-image: url('./images/wallets/trustwallet2x.png');
      }
      &:after {
        content: 'Trust Wallet';
      }
    }
    .metamask {
      &:before {
        background-image: url('./images/wallets/metamask2x.png');
      }
      &:after {
        content: 'MetaMask';
      }
    }
    .huh {
      display: none;
      &:before {
        background-image: url('./images/wallets/huh2x.png');
      }
      &:after {
        content: 'Huh?';
      }
    }
  }
}
