@import url('https://fonts.googleapis.com/css2?family=Racing+Sans+One&display=swap');

html, body {
  margin: 0;
  padding: 0;
  font-size: 1vw;
  background: #000;
  font-family: sans-serif;
  overflow-x: hidden;
  touch-action: pan-y;
}

body {
  font-size: 3.6rem;
  color: #fff;
  overflow: hidden;
  &:before {
    content: '';
    display: block;
    background: #000;
    background-image: url(./hill2.png);
    background-size: auto 80vh;
    background-position: bottom center;
    background-position: bottom 0 left 50%;
    background-attachment: fixed;
    background-repeat: no-repeat;
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
  }
}

html.ios body:before {
  position: absolute;
  background-attachment: initial;
  background-size: auto calc(100vh);
  transform: translateY(40rem);
}

@media (orientation: landscape) {
  html {
    font-size: 5px;
  }
  body:before {
    background-size: 100vw auto;
    background-position: bottom center;
    background-position: bottom 0 left 50%;
  }

  svg.king-svg {
    height: 100px;
  }

  .about {
    display: flex;
    flex-direction: column;
    align-items: center;
    > * {
      flex: 0 0 auto;
      max-width: 800px;
    }
  }
}

input {
  font-size: 3.6rem;
}

#logo {
  width: 90rem;
  height: auto;
  padding-right: 3rem;
}

.hero {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 60rem;
  position: relative;
  // overflow: hidden;
  // position: fixed;
  // justify-content: space-around;

  @media (orientation: landscape) {
    justify-content: center;
  }
}

.foreground {
  margin-top: -60rem;
  height: 60rem;
  display: flex;
  flex-direction: row;
  position: relative;
  background: linear-gradient(to top, #000, rgba(0,0,0,0) 50%);
  pointer-events: none;
  opacity: 0.9;

  .ios & {
    margin-top: -80rem;
  }

  &:before {
    content: '';
    display: block;
    background: url('./images/foreground/left.png');
    height: 60rem;
    width: 50%;
    flex: 1 1 auto;
    background-size: contain;
    background-position: left bottom;
    background-repeat: no-repeat;
  }
  &:after {
    content: '';
    display: block;
    background: url('./images/foreground/right.png');
    height: 60rem;
    width: 50%;
    flex: 1 1 auto;
    background-size: contain;
    background-position: right bottom;
    background-repeat: no-repeat;
  }


  @media (orientation: landscape) {
    justify-content: space-between;

    &:before, &:after {
      background-size: cover;
      width: 60rem;
      flex: 0 0 auto;
    }
    &:before {
      background-position: top left;
    }
    &:after {
      background-position: top right;
    }
  }
}

.about {
  background: #000;
  position: relative;
  min-height: 60rem;
  padding: 12rem 7rem;
  opacity: 0.9;
}

.king-container{
  display: flex;
  flex-direction: column;
  align-items: center;

  &:before {
    content: 'Current King';

    transition: opacity 0.4s 0.4s, transform 0.4s 0.4s;
    .init &{
      opacity: 0;
      transform: translateY(2rem);
    }
  }

  .health-bar {
    // margin-top: -1rem;
    margin-top: -3rem;

    transition: width 0.5s, opacity 0.2s;
    .init &{
      width: 0.9rem;
      opacity: 0;
    }
  }
}

.take-the-hill .king-container {
  display: none;
}

.king-svg {
  width: 100vw;
  height: auto;
  background: radial-gradient(ellipse at center, rgba(255,255,128,0.5) 0%, rgba(255,128,0,0.35) 40%,rgba(255,128,0,0) 59%);
  // background: radial-gradient(ellipse at center, hsl(190deg 100% 85% / 50%) 0%, hsl(180deg 100% 50% / 35%) 40%, hsl(180deg 100% 50% / 0%) 59%);
  background: radial-gradient(ellipse at center, rgba(179, 242, 255, 0.5) 0%, rgb(0 255 255 / 14%) 40%, rgba(0, 255, 255, 0) 59%);
  background-size: 200% 100%;
  background-position: center;
  background-repeat: no-repeat;
  // background: url("data:image/svg+xml;utf8,<svg class='editorial' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' preserveAspectRatio='none' viewBox='0 0 360 60'></svg>");

  transition: background 1s, opacity 1s;

  .init & {
    opacity: 0;
    // background: radial-gradient(ellipse at center, rgba(255,255,128,0.5) 0%, rgba(255,128,0,0.35) 40%,rgba(255,128,0,0) 59%);
    background-size: 50% 100%;
    background-position: center;
    background-repeat: no-repeat;
  }
}

html.not-ios .init #king {
  transform: translateY(-0.4rem);
  transition: transform 0.5s 0s;
  .init & {
    transform: translateY(3rem);
  }
}
#king {
  fill: #fff;
  stroke: #000;
  font-weight: 100;
  text-shadow: 0 0 2rem rgb(255 128 0 / 50%);
  text-shadow: 0 0 2rem hsl(180deg 100% 50% / 50%);
  paint-order: stroke fill;
  stroke-width: 0.3rem;
  font-family: 'Racing Sans One', cursive;

  // transform: translateY(-1rem);
}

.health-bar {
  height: 0.9rem;
  width: 86vw;
  min-width: fit-content;
  max-width: 86vw;

  @media (orientation: landscape) {
    max-width: 160rem;
  }
  // border: 0.2rem solid #d08a43;
  // border: 0.2rem solid #522900;
  // border: 0.2rem solid #6b3a08;
  border: 0.2rem solid hsl(180deg 86% 23%);
  border-radius: 0.45rem;
  background: #1e1205;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  position: relative;
  z-index: 1;
  // box-shadow: 0 0 2rem rgb(255 128 0 / 25%);
  box-shadow: 0 0 2rem hsl(180deg 100% 50% / 25%);

  height: 3rem;
  border-radius: 1.5rem;
  box-shadow: 0 0 1rem rgb(0 255 255 / 40%), 0 0 3rem rgb(0 255 255 / 50%), 0 0 1rem rgb(0 255 255 / 40%) inset;
  background: rgba(0,0,0,0.8);

  &:before {
    content: "";
    display: block;
    // width: 0.5rem;
    // height: 0.5rem;
    // border-radius: 0.245rem;
    // background: #d08a43;
    // background: #6b3a08;
    // background: hsl(180deg 86% 23%);
    padding: 0;
    flex: 1 0 auto;
    height: 2.6rem;
    width: 2.6rem;
    border-radius: 1.3rem;
    background: #fff;
    box-shadow: 0 0 0.2rem 0.1rem #086d6d, 0 0 1.2rem 0.5rem #086d6d inset;
  }

  .decay {
    flex: 0 1 100%;
  }
}


