.hp-reading {
  display: block;
  align-self: center;
  width: 86vw;
  min-width: fit-content;
  max-width: 86vw;
  @media (orientation: landscape) {
    max-width: 160rem;
  }
  box-sizing: border-box;
  padding: 0.5rem 1rem;
  font-size: 2.5rem;
  font-family: sans-serif;
  // font-weight: 100;
  // color: #e9b077; //rgb(255 128 0 / 100%);
  // color: #d08a43; //rgb(255 128 0 / 100%);
  // text-shadow: 0 0 2rem rgb(255 128 0 / 25%);

  // color: hsl(180deg 60% 54%);
  color: hsl(180deg 40% 54%);
  text-shadow: 0 0 2rem hsl(180deg 100% 50% / 25%);

  transition: opacity 0.2s 0.3s, transform 0.2s 0.3s;
  .init &{
    opacity: 0;
    transform: translateY(-2rem);
  }
}

h3 {
  font-size: 4rem;
  font-weight: 300;
  margin: 5rem 1rem 1rem 1rem;
}

.links {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 8rem;

  a {
    padding: 3rem;
    &:before {
      content: '';
      background-image: url(./images/link@2x_t.png);
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      width: 20rem;
      height: 20rem;
      display: block;
      opacity: 0.8;
    }

    transition: opacity 0.5s 0.3s, transform 0.5s 0.3s;

    + a {
      transition: opacity 0.5s 0.35s, transform 0.5s 0.35s;
    }
    + a + a {
      transition: opacity 0.5s 0.4s, transform 0.5s 0.4s;
    }
    + a + a + a {
      transition: opacity 0.5s 0.45s, transform 0.5s 0.45s;
    }
    + a + a + a + a {
      transition: opacity 0.5s 0.5s, transform 0.5s 0.5s;
    }
    + a + a + a + a + a {
      transition: opacity 0.5s 0.55s, transform 0.5s 0.55s;
    }

    .init &{
      opacity: 0;
      transform: scale(0.5);
    }

  }
  .twitter-com {
    &:before {
      background-image: url(./images/twitter@2x_t.png);
    }
  }
  .tiktok-com {
    &:before {
      background-image: url(./images/tiktok@2x_t.png);
    }
  }
  .t-me {
    &:before {
      background-image: url(./images/telegram@2x_t.png);
    }
  }
  .github-com, .github-io {
    &:before {
      background-image: url(./images/github@2x_t.png);
    }
  }
  .instagram-com {
    &:before {
      background-image: url(./images/instagram@2x_t.png);
    }
  }
  .twitch-tv {
    &:before {
      background-image: url(./images/twitch@2x_t.png);
    }
  }
  .discord-gg {
    &:before {
      background-image: url(./images/discord@2x_t.png);
    }
  }
  .youtube-com {
    &:before {
      background-image: url(./images/youtube@2x_t.png);
    }
  }
  .reddit-com {
    &:before {
      background-image: url(./images/reddit@2x_t.png);
    }
  }
  .pancakeswap-finance {
    &:before {
      background-image: url(./images/pancakeswap@2x_t.png);
    }
  }
}

button#take, .king-buttons a {
  display: block;
  text-decoration: none;
  height: 15rem;
  line-height: 15rem;
  font-size: 5rem;
  font-weight: 600;
  border-radius: 7.5rem;
  padding: 0 8rem 0 7rem;
  color: #FFF;
  // background: #291C0C;
  background: rgba(0,0,0,0.5);
  border: 0.5rem solid rgba(192,255,255,0.5);
  cursor: pointer;
  margin: 7rem;

  transition: opacity 0.5s 0.5s, transform 0.5s 0.5s;
  .init &{
    opacity: 0;
    transform: translateY(2rem);
  }
}

.king-buttons, .king-view button#take {
  display: none;
}

.king-view .king-buttons {
  display: flex;
  justify-content: center;
}

.king-buttons {
  flex-direction: row;

  a {
    margin: 7rem 3rem;
    flex: 0 0 auto;
  }
}

button#submit {
  height: 15rem;
  line-height: 15rem;
  font-size: 5rem;
  font-weight: 600;
  border-radius: 7.5rem;
  padding: 0 8rem 0 7rem;
  color: #ddd;
  background: rgba(0,0,0,0.5);
  border: 0.5rem solid #ccc;
  cursor: pointer;
  margin: 7rem;
  align-self: center;
}



