.label {
  display: block;
  text-align: center;
  font-size: 4rem;
  color: #799191;
  margin: 1rem 0 1rem 0;
}

.input-wrapper {
  flex: 1 1 auto;
}

.input-wrapper, input[type=button] {
  border: 0.4rem solid #ccc;
  border-radius: 2.4rem;
  background: rgba(0,0,0,0.6);
  margin: 1rem 0 4rem 0;
  line-height: 10rem;
}

.input-wrapper {
  position: relative;
  &:after {
    position: absolute;
    line-height: inherit;
    right: 0;
    padding-right: 4rem;
  }
}

input {
  font-size: 4.5rem;
  line-height: inherit;
  padding: 0 3rem;
  background: transparent;
  border: none;
  outline: none;
  color: #799191;
  margin: 0;
  width: 100%;
  box-sizing: border-box;

  &[type=button] {
    font-size: 6rem;
    color: #fff;
  }
}

.input-wrapper + input[type=button] {
  margin-left: 3rem;
  flex: 0 0 auto;
}

input[type=button].remove
// ,input[type=button].add
{
  width: 10.8rem;
  padding: 0;
  text-align: center;
}

input[type=button].add {
  width: auto;
  margin: 0 auto;
  align-self: center;
}

.link-input +
.link-input +
.link-input +
.link-input +
.link-input +
.link-input + .add {
  display: none;
}

.input-row {
  display: flex;
  flex-direction: row;
}

.exchangeIcon {
  width: auto;
  height: 6rem;
  padding: 3rem 2rem;
}

.king-form {
  width: 80rem;
  display: flex;
  flex-direction: column;
  align-items: stretch;

  display: none;
  .take-the-hill & {
    display: flex;
  }

  .input-wrapper.hp-input-wrapper:after {
      content: 'HP';
  }

  .input-wrapper.value-input-wrapper:after {
      content: 'BNB';
  }

  .exchangePath {
    fill: #B5B5B5;
  }

  input:after {
    content: 'test';
  }
}

.profile-modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9;
  height: 100vh;
  width: 100vw;
  background: rgba(0,0,0,0.85);
  padding: 3rem 0;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  box-sizing: border-box;
  // display: none;

  @media (orientation: landscape) {
    align-items: center;
    overflow: visible;
  }

  opacity: 0;
  pointer-events: none;
  transition: opacity 0.5s, display 0.5s;

  .profile-view & {
    pointer-events: auto;
    opacity: 1;
    display: flex;
  }

  a#profileCloseButton {
    color: #fff;
    text-decoration: none;
    height: 15rem;
    line-height: 15rem;
    font-size: 8rem;
    text-align: center;
    width: 15rem;
    border-radius: 50%;
    border: .5rem solid #fff;
    background: rgba(0,0,0,0.8);

    display: block;
    position: absolute;
    bottom: 20rem;
    left: 50%;
    transform: translateX(-50%);
    z-index: 999;

    @media (orientation: landscape) {
      top: 10rem;
      right: 10rem;
      left: initial;
      bottom: initial;
    }
  }
}

.profile {
  #profileName,
  .profile-stats,
  #profileReigns {
    transition: opacity 0.5s;
    body.loading & {
      transition: opacity 0.25s;
      opacity: 0;
      pointer-events: none;
    }
  }
}

.profile {
  padding: 7rem;
  box-sizing: border-box;
  width: auto;
  display: flex;
  flex-direction: column;
  height: 100vh;

  @media (orientation: landscape) {
    padding-bottom: 0rem;
  }

  @media (orientation: landscape) {
    width: 100%;
    max-width: 100rem;
  }

  > * {
    flex: 0 0 auto;
  }

  #profileName {
    text-align: center;
    height: 8rem;
  }

  .profile-stats {
    display: flex;
    flex-direction: row;
    height: 8rem;
    overflow: hidden;
    border-radius: 2.4rem;
    padding: 2rem 4rem;
    border: 0.5rem solid #ffffff88;
    background: #000000cc;
    justify-content: space-between;
    justify-content: space-around;
    font-weight: bold;
    > div {
      display: flex;
      flex-direction: column;
      font-size: 5rem;
      &:before {
        color: #ffffff88;
        font-size: 3rem;
        text-transform: uppercase;
      }
    }

    // &:before, &:after {
    //   content: '';
    //   display: block;
    //   width: 2rem;
    //   flex: 0 1 auto;
    // }
  }

  #profileCollectedCrowns {
    &:before {
      content: 'Crowns:';
    }
  }

  #profileHighestHP {
    &:before {
      content: 'Most HP:';
    }
  }

  #profileReignTime {
    &:before {
      content: 'King Hours:';
    }
  }

  #profileReigns {
    margin: 0 -7rem;
    padding: 0 7rem;
    overflow: hidden;
    overflow-y: auto;
    flex: 0 1 auto;
    padding-bottom: 35rem;

    @media (orientation: landscape) {
      padding-bottom: 7rem;
    }
  }

  .health-bar {
    border: 0.2rem solid hsl(180deg 0% 60%);
    background: rgba(0,0,0,0.8);
    position: relative;
    box-shadow: 0 0 1rem hsl(180deg 100% 00% / 50%);
  }

  #reignNavigation {
    display: flex;
    flex-direction: row;
    margin: 1rem -7rem 0 -7rem;
    border: 0.2rem solid #ccc;
    border-width: 0.2rem 0;
    line-height: 8rem;
    border-bottom-color: #7cc;
    margin-top: 6rem;

    box-shadow: 0 0.5rem 0.5rem #77ffff55;

    @media (orientation: landscape) {
      border-width: 0.2rem;
      border-radius: 4rem;
    }

    #previousReignButton {
      padding: 0 7rem;
      &:before {
        content: '< prev';
      }
    }
    #currentReignName {
      flex: 1 0 auto;
      text-align: center;
      color: #fff;
      text-shadow: 0rem 0rem 1rem #7cc;

      &:before {
        content: 'Reign #: ';
      }
    }
    #nextReignButton {
      padding: 0 7rem;
      &:before {
        content: 'next >';
      }
    }
  }

  .reign {

    margin: 1rem -3rem;
    border: 0.5rem solid #fff;
    padding: 3rem;
    border-radius: 3rem;

    &.selected {
      // color: #7cc;
      background: #113333ff;
      box-shadow: 0 0 2rem 1rem #77ffff55, 0 0 2rem 1rem #77ffff55 inset;
    }

    .health-bar {
      width: auto;
    }

    .reign-top-bar {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      margin-bottom: 3rem;
    }

    .reign-start, .reign-duration, .reign-index {
      display: flex;
      flex-direction: column;
      flex: 0 1 auto;
      &:before {
        font-size: 3rem;
        color: #ffffff88;
        text-transform: uppercase;
      }
    }

    .reign-start {
      &:before {
        content: 'Beginning: ';
      }
    }
    .reign-duration {
      &:before {
        content: 'Duration: ';
      }
    }

    .reign-index {
      &:before {
        content: 'Reign #';
      }
    }
  }

}

