  text-decoration: none;
  vertical-align: text-bottom;
  text-align: center;
  display: block;
  line-height: 9rem;

  font-size: 4rem;
  @media (orientation: landscape) {
    font-size: 5rem;
  }

  color: #aff;
  margin-bottom: 10rem;
  &:before {
    background-image: url(./images/twitch@2x_t.png);
    background-size: contain;
    vertical-align: text-bottom;
    margin-right: 2.4rem;
